import axios from 'axios';
import { message } from 'antd';

import type {
    optionsParams,
    myAxiosRequestConfig,
    // myAxiosResponse,
} from './axios.type';
import type { AxiosError, AxiosResponse } from 'axios';

function Toast(text: string) {
    // ElMessage({
    //     type: 'error',
    //     message: text,
    // });
    message.error(text)
}

function requestError(code: number): string {
    // const statusCode = Number(code);
    switch (code) {
        case 400:
            return 'Bad Request ()';
        case 401:
            return 'Unauthorized ()';
        case 403:
            return 'Forbidden ()';
        case 404:
            return 'NOT Found ()';
        case 405:
            return 'Bad Request ()';
        case 406:
            return 'Not Acceptable ()';
        case 407:
            return 'Proxy Authentication Required ()';
        case 408:
            return 'Request Timed-Out ()';
        case 409:
            return 'Conflict ()';
        case 410:
            return 'Gone ()';
        case 411:
            return 'Length Required ()';
        case 412:
            return 'Precondition Failed ()';
        case 413:
            return 'Request Entity Too Large ()';
        case 414:
            return 'Request, URI Too Large ( URI )';
        case 415:
            return 'Unsupported Media Type ()';
        case 429:
            return ',';
        case 500:
            return 'Internal Server Error ()';
        case 501:
            return 'Not Implemented ()';
        case 502:
            return 'Bad Gateway ()';
        case 503:
            return 'Server Unavailable ()';
        case 504:
            return 'Gateway Timed-Out ()';
        case 505:
            return 'HTTP Version not supported (HTTP )';
        default:
            return '';
    }
}

const CancelToken = axios.CancelToken;
const reqConfig = {
    baseURL: '/', // process.env.baseURL || process.env.apiUrl || ""
    timeout: 40 * 1000, // Timeout
};

const Axios = axios.create(reqConfig);

